"use client";

import { useState, useEffect } from "react";
import { Calendar as CalendarIcon, Download } from "lucide-react";

export default function CalendarTab() {
    const [events, setEvents] = useState([]);
    const [selectedDate, setSelectedDate] = useState(new Date());
    const [view, setView] = useState<"month" | "week" | "day">("month");

    useEffect(() => {
        fetchEvents();
    }, []);

    const fetchEvents = async () => {
        try {
            const token = localStorage.getItem("token");
            const response = await fetch("http://localhost:3000/api/events", {
                headers: {
                    Authorization: `Bearer ${token}`,
                },
            });

            if (response.ok) {
                const data = await response.json();
                setEvents(data);
            }
        } catch (error) {
            console.error("Failed to fetch events:", error);
        }
    };

    const handleExportCalendar = () => {
        const token = localStorage.getItem("token");
        window.open(`http://localhost:3000/api/ics/my-calendar.ics`, "_blank");
    };

    const getDaysInMonth = (date: Date) => {
        const year = date.getFullYear();
        const month = date.getMonth();
        const firstDay = new Date(year, month, 1).getDay();
        const daysInMonth = new Date(year, month + 1, 0).getDate();
        const days = [];

        for (let i = 0; i < firstDay; i++) {
            days.push(null);
        }

        for (let i = 1; i <= daysInMonth; i++) {
            days.push(i);
        }

        return days;
    };

    const renderCalendar = () => {
        const days = getDaysInMonth(selectedDate);
        const weeks = [];
        for (let i = 0; i < days.length; i += 7) {
            weeks.push(days.slice(i, i + 7));
        }

        return (
            <div>
                <div
                    style={{
                        display: "flex",
                        justifyContent: "space-between",
                        alignItems: "center",
                        marginBottom: "1.5rem",
                    }}
                >
                    <h2
                        style={{
                            fontSize: "1.5rem",
                            fontWeight: "600",
                            color: "rgb(17, 24, 39)",
                        }}
                    >
                        {selectedDate.toLocaleDateString("en-US", {
                            month: "long",
                            year: "numeric",
                        })}
                    </h2>
                    <div style={{ display: "flex", gap: "0.5rem" }}>
                        <button
                            onClick={() =>
                                setSelectedDate(
                                    new Date(
                                        selectedDate.getFullYear(),
                                        selectedDate.getMonth() - 1,
                                    ),
                                )
                            }
                            style={{
                                padding: "0.5rem 0.75rem",
                                border: "1px solid rgb(209, 213, 219)",
                                borderRadius: "0.375rem",
                                background: "white",
                                cursor: "pointer",
                            }}
                        >
                            Previous
                        </button>
                        <button
                            onClick={() =>
                                setSelectedDate(
                                    new Date(
                                        selectedDate.getFullYear(),
                                        selectedDate.getMonth() + 1,
                                    ),
                                )
                            }
                            style={{
                                padding: "0.5rem 0.75rem",
                                border: "1px solid rgb(209, 213, 219)",
                                borderRadius: "0.375rem",
                                background: "white",
                                cursor: "pointer",
                            }}
                        >
                            Next
                        </button>
                    </div>
                </div>

                <div
                    style={{
                        display: "grid",
                        gridTemplateColumns: "repeat(7, 1fr)",
                        gap: "0.25rem",
                        marginBottom: "0.5rem",
                    }}
                >
                    {["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"].map(
                        (day) => (
                            <div
                                key={day}
                                style={{
                                    textAlign: "center",
                                    fontWeight: "600",
                                    padding: "0.5rem",
                                    backgroundColor: "rgb(249, 250, 251)",
                                }}
                            >
                                {day}
                            </div>
                        ),
                    )}
                </div>

                {weeks.map((week, weekIndex) => (
                    <div
                        key={weekIndex}
                        style={{
                            display: "grid",
                            gridTemplateColumns: "repeat(7, 1fr)",
                            gap: "0.25rem",
                        }}
                    >
                        {week.map((day, dayIndex) => (
                            <div
                                key={dayIndex}
                                style={{
                                    aspectRatio: "1",
                                    border: "1px solid rgb(229, 231, 235)",
                                    borderRadius: "0.25rem",
                                    padding: "0.5rem",
                                    backgroundColor: day
                                        ? "white"
                                        : "rgb(249, 250, 251)",
                                }}
                            >
                                {day && (
                                    <>
                                        <div style={{ fontWeight: "600" }}>
                                            {day}
                                        </div>
                                        <div
                                            style={{
                                                fontSize: "0.75rem",
                                                color: "rgb(107, 114, 128)",
                                                marginTop: "0.25rem",
                                            }}
                                        >
                                            2 events
                                        </div>
                                    </>
                                )}
                            </div>
                        ))}
                    </div>
                ))}
            </div>
        );
    };

    return (
        <div
            style={{ display: "flex", flexDirection: "column", gap: "1.5rem" }}
        >
            <div
                style={{
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                }}
            >
                <h2
                    style={{
                        fontSize: "1.5rem",
                        fontWeight: "600",
                        color: "rgb(17, 24, 39)",
                    }}
                >
                    <CalendarIcon
                        style={{
                            width: "1.5rem",
                            height: "1.5rem",
                            marginRight: "0.5rem",
                            verticalAlign: "middle",
                        }}
                    />
                    My Calendar
                </h2>
                <button
                    onClick={handleExportCalendar}
                    className="button button-primary"
                >
                    <Download
                        style={{
                            width: "1.25rem",
                            height: "1.25rem",
                            marginRight: "0.5rem",
                        }}
                    />
                    Export ICS
                </button>
            </div>

            <div
                style={{
                    display: "flex",
                    gap: "0.5rem",
                    marginBottom: "1.5rem",
                }}
            >
                {[
                    { view: "month" as const, label: "Month" },
                    { view: "week" as const, label: "Week" },
                    { view: "day" as const, label: "Day" },
                ].map(({ view: v, label }) => (
                    <button
                        key={v}
                        onClick={() => setView(v)}
                        style={{
                            padding: "0.75rem 1rem",
                            borderRadius: "0.375rem",
                            border:
                                view === v
                                    ? "none"
                                    : "1px solid rgb(209, 213, 219)",
                            backgroundColor:
                                view === v ? "rgb(37, 99, 235)" : "white",
                            color: view === v ? "white" : "rgb(55, 65, 81)",
                            cursor: "pointer",
                        }}
                    >
                        {label}
                    </button>
                ))}
            </div>

            {view === "month" && renderCalendar()}
            {view === "week" && (
                <div className="card">Week view coming soon</div>
            )}
            {view === "day" && <div className="card">Day view coming soon</div>}
        </div>
    );
}
