"use client";

import { useState, useEffect } from "react";
import { UserPlus, UserMinus, Check, X, Users } from "lucide-react";

interface Friend {
    id: string;
    user1: { id: string; email: string; name?: string };
    user2: { id: string; email: string; name?: string };
    status: string;
    createdAt: string;
}

export default function FriendsTab() {
    const [friends, setFriends] = useState<Friend[]>([]);
    const [showAddFriend, setShowAddFriend] = useState(false);
    const [friendEmail, setFriendEmail] = useState("");
    const [isLoading, setIsLoading] = useState(false);

    useEffect(() => {
        fetchFriends();
    }, []);

    const fetchFriends = async () => {
        try {
            const token = localStorage.getItem("token");
            const response = await fetch("http://localhost:3000/api/friends/", {
                headers: {
                    Authorization: `Bearer ${token}`,
                },
            });

            if (response.ok) {
                const data = await response.json();
                setFriends(data);
            }
        } catch (error) {
            console.error("Failed to fetch friends:", error);
        }
    };

    const handleAddFriend = async () => {
        if (!friendEmail) return;
        setIsLoading(true);

        try {
            const token = localStorage.getItem("token");
            const response = await fetch(
                "http://localhost:3000/api/friends/request",
                {
                    method: "POST",
                    headers: {
                        "Content-Type": "application/json",
                        Authorization: `Bearer ${token}`,
                    },
                    body: JSON.stringify({ userId: friendEmail }),
                },
            );

            if (response.ok) {
                setShowAddFriend(false);
                setFriendEmail("");
                fetchFriends();
            }
        } catch (error) {
            console.error("Failed to add friend:", error);
        } finally {
            setIsLoading(false);
        }
    };

    const handleAcceptFriend = async (friendshipId: string) => {
        try {
            const token = localStorage.getItem("token");
            const response = await fetch(
                `http://localhost:3000/api/friends/${friendshipId}/accept`,
                {
                    method: "POST",
                    headers: {
                        Authorization: `Bearer ${token}`,
                    },
                },
            );

            if (response.ok) {
                fetchFriends();
            }
        } catch (error) {
            console.error("Failed to accept friend:", error);
        }
    };

    const handleRemoveFriend = async (friendshipId: string) => {
        try {
            const token = localStorage.getItem("token");
            const response = await fetch(
                `http://localhost:3000/api/friends/${friendshipId}`,
                {
                    method: "DELETE",
                    headers: {
                        Authorization: `Bearer ${token}`,
                    },
                },
            );

            if (response.ok) {
                fetchFriends();
            }
        } catch (error) {
            console.error("Failed to remove friend:", error);
        }
    };

    const getCurrentUserId = (): string | null => {
        const token = localStorage.getItem("token");
        if (!token) return null;
        try {
            const payload = JSON.parse(atob(token.split(".")[1]));
            return payload.sub;
        } catch {
            return null;
        }
    };

    const getFriendInfo = (friendship: Friend) => {
        const currentUserId = getCurrentUserId();
        return friendship.user1.id === currentUserId
            ? friendship.user2
            : friendship.user1;
    };

    const isPendingRequest = (friendship: Friend) => {
        const currentUserId = getCurrentUserId();
        return (
            friendship.status === "pending" &&
            friendship.user1.id === currentUserId
        );
    };

    const isIncomingRequest = (friendship: Friend) => {
        const currentUserId = getCurrentUserId();
        return (
            friendship.status === "pending" &&
            friendship.user2.id === currentUserId
        );
    };

    return (
        <div
            style={{ display: "flex", flexDirection: "column", gap: "1.5rem" }}
        >
            <div
                style={{
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                }}
            >
                <h2
                    style={{
                        fontSize: "1.5rem",
                        fontWeight: "600",
                        color: "rgb(17, 24, 39)",
                    }}
                >
                    ðŸ‘¥ Friends
                </h2>
                <button
                    onClick={() => setShowAddFriend(true)}
                    className="button button-primary"
                >
                    <UserPlus
                        style={{
                            width: "1.25rem",
                            height: "1.25rem",
                            marginRight: "0.5rem",
                        }}
                    />
                    Add Friend
                </button>
            </div>

            {friends.length === 0 ? (
                <div
                    className="card"
                    style={{ textAlign: "center", padding: "3rem" }}
                >
                    <Users
                        size={64}
                        style={{
                            color: "rgb(209, 213, 219)",
                            marginBottom: "1rem",
                        }}
                    />
                    <h3
                        style={{
                            fontSize: "1.25rem",
                            fontWeight: "600",
                            marginBottom: "0.5rem",
                            color: "rgb(17, 24, 39)",
                        }}
                    >
                        No friends yet
                    </h3>
                    <p style={{ color: "rgb(107, 114, 128)" }}>
                        Click "Add Friend" to get started
                    </p>
                </div>
            ) : (
                <div className="card">
                    <div
                        style={{
                            display: "flex",
                            flexDirection: "column",
                            gap: "1rem",
                        }}
                    >
                        {friends.map((friendship) => {
                            const friend = getFriendInfo(friendship);
                            const isPending = isPendingRequest(friendship);
                            const isIncoming = isIncomingRequest(friendship);

                            return (
                                <div
                                    key={friendship.id}
                                    style={{
                                        padding: "1.25rem",
                                        border: "1px solid rgb(229, 231, 235)",
                                        borderRadius: "0.5rem",
                                        backgroundColor: "rgb(249, 250, 251)",
                                    }}
                                >
                                    <div
                                        style={{
                                            display: "flex",
                                            justifyContent: "space-between",
                                            alignItems: "flex-start",
                                            marginBottom: "0.75rem",
                                        }}
                                    >
                                        <div>
                                            <h4
                                                style={{
                                                    fontWeight: "600",
                                                    color: "rgb(17, 24, 39)",
                                                    marginBottom: "0.25rem",
                                                }}
                                            >
                                                {friend.name || friend.email}
                                            </h4>
                                            <p
                                                style={{
                                                    color: "rgb(107, 114, 128)",
                                                    fontSize: "0.875rem",
                                                }}
                                            >
                                                {friend.email}
                                            </p>
                                        </div>
                                        <span
                                            className={`badge ${friendship.status === "accepted" ? "badge-green" : "badge-yellow"}`}
                                        >
                                            {friendship.status === "accepted"
                                                ? "Friends"
                                                : "Pending"}
                                        </span>
                                    </div>

                                    <div
                                        style={{
                                            display: "flex",
                                            gap: "0.5rem",
                                        }}
                                    >
                                        {isIncoming && (
                                            <>
                                                <button
                                                    onClick={() =>
                                                        handleAcceptFriend(
                                                            friendship.id,
                                                        )
                                                    }
                                                    className="button button-success"
                                                    style={{
                                                        padding:
                                                            "0.375rem 0.75rem",
                                                        fontSize: "0.75rem",
                                                    }}
                                                >
                                                    <Check
                                                        style={{
                                                            width: "1rem",
                                                            height: "1rem",
                                                            marginRight:
                                                                "0.25rem",
                                                        }}
                                                    />
                                                    Accept
                                                </button>
                                                <button
                                                    onClick={() =>
                                                        handleRemoveFriend(
                                                            friendship.id,
                                                        )
                                                    }
                                                    className="button button-danger"
                                                    style={{
                                                        padding:
                                                            "0.375rem 0.75rem",
                                                        fontSize: "0.75rem",
                                                    }}
                                                >
                                                    <X
                                                        style={{
                                                            width: "1rem",
                                                            height: "1rem",
                                                            marginRight:
                                                                "0.25rem",
                                                        }}
                                                    />
                                                    Decline
                                                </button>
                                            </>
                                        )}

                                        {friendship.status === "accepted" && (
                                            <button
                                                onClick={() =>
                                                    handleRemoveFriend(
                                                        friendship.id,
                                                    )
                                                }
                                                className="button button-danger"
                                                style={{
                                                    padding: "0.375rem 0.75rem",
                                                    fontSize: "0.75rem",
                                                }}
                                            >
                                                <UserMinus
                                                    style={{
                                                        width: "1rem",
                                                        height: "1rem",
                                                        marginRight: "0.25rem",
                                                    }}
                                                />
                                                Remove
                                            </button>
                                        )}

                                        {isPending && (
                                            <span
                                                style={{
                                                    backgroundColor:
                                                        "rgb(254, 249, 195)",
                                                    color: "rgb(161, 98, 7)",
                                                    padding: "0.25rem 0.75rem",
                                                    borderRadius: "9999px",
                                                    fontSize: "0.75rem",
                                                    fontWeight: "500",
                                                }}
                                            >
                                                Request sent
                                            </span>
                                        )}
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                </div>
            )}

            {showAddFriend && (
                <div className="card">
                    <h3
                        style={{
                            fontSize: "1.25rem",
                            fontWeight: "600",
                            marginBottom: "1rem",
                            color: "rgb(17, 24, 39)",
                        }}
                    >
                        Add Friend
                    </h3>
                    <div
                        style={{
                            display: "flex",
                            gap: "1rem",
                            alignItems: "end",
                        }}
                    >
                        <div style={{ flex: 1 }}>
                            <label
                                style={{
                                    display: "block",
                                    fontSize: "0.875rem",
                                    fontWeight: "500",
                                    marginBottom: "0.5rem",
                                    color: "rgb(55, 65, 81)",
                                }}
                            >
                                Friend's Email
                            </label>
                            <input
                                type="email"
                                value={friendEmail}
                                onChange={(e) => setFriendEmail(e.target.value)}
                                className="input"
                                placeholder="friend@example.com"
                            />
                        </div>
                        <div style={{ display: "flex", gap: "0.5rem" }}>
                            <button
                                onClick={handleAddFriend}
                                disabled={isLoading || !friendEmail}
                                className="button button-primary"
                                style={{ flex: 1 }}
                            >
                                {isLoading ? "Sending..." : "Send Request"}
                            </button>
                            <button
                                onClick={() => {
                                    setShowAddFriend(false);
                                    setFriendEmail("");
                                }}
                                className="button button-secondary"
                                style={{ padding: "0.5rem 1rem" }}
                            >
                                Cancel
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}
